import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { HttpClient, HttpResponse, HttpHeaders } from '@angular/common/http';

import { Etudiant } from '../models/etudiant';
import { Enseignant } from '../models/enseignant';


@Injectable()
export class UserService {

  constructor(private http: HttpClient) { }

  etudiants(): Observable<HttpResponse<any>> {
    return this.http.get<any>('http://localhost:8080/bank-api/etudiant', { observe: 'response' });
  }

  enseignants(): Observable<HttpResponse<any>> {
    return this.http.get<any>('http://localhost:8080/bank-api/enseignant', { observe: 'response' });
  }

  connect(dep: string, role: string, matricule: string, password: string, regist: number): Observable<HttpResponse<any>> {
    return this.http.get<any>('http://localhost:8080/bank-api/'+role+'/auth/'+dep+'/'+matricule+'/'+password+'/'+regist, { observe: 'response' });
  }

  inscript(dep: string, role: string, matricule: string, cni: string, regist: number): Observable<HttpResponse<any>> {
    return this.http.get<any>('http://localhost:8080/bank-api/'+role+'/auth1/'+dep+'/'+matricule+'/'+cni+'/'+regist, { observe: 'response' });
  }

  sendEmail(email: string, lastname: string): Observable<HttpResponse<any>> {
    return this.http.get<any>('http://localhost:8080/bank-api/compte/sendmail/'+email+'/'+lastname, { observe: 'response' });
  }

  updateStudent(student: Etudiant): Observable<Etudiant> {
    return this.http.put<Etudiant>('http://localhost:8080/bank-api/etudiant', student);
  }

  updateTeacher(teacher: Enseignant): Observable<Enseignant> {
    return this.http.put<Enseignant>('http://localhost:8080/bank-api/enseignant', teacher);
  }

  /*addClient(client: User): Observable<User> {
   return this.http.post<User>('http://localhost:8080/bankinf-api/users', client);
 }*/

}



