import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';

import { DialogModule } from 'primeng/dialog';
import { InputTextModule } from 'primeng/inputtext';
import { KeyFilterModule } from 'primeng/keyfilter';
import { SelectButtonModule } from 'primeng/selectbutton';

import { FormGroup, FormControl, Validators} from '@angular/forms';
import { PasswordValidator } from '../validators/password.validator';

import { UtilService } from '../services/util.service';
import { UserService } from '../services/user.service';

import { SelectItem } from 'primeng/api';
import { Etudiant } from '../models/etudiant';
import { Enseignant } from '../models/enseignant';

@Component({
  selector: 'app-department',
  templateUrl: './department.component.html',
  providers: [UserService, UtilService],
  styleUrls: ['./department.component.scss']
})
export class DepartmentComponent implements OnInit
{
  formreg: FormGroup;
  formlog: FormGroup;

  etud = new Etudiant(0, '', '', '', '', '', '', '', null, 0, null)
  ens = new Enseignant(0, '', '', '', '', '', '', '', null, 0, "");
  firstname: string;
  lastname: string;
  email: string;

  display_inscrip: boolean = false;
  display_membre: boolean = false;
  display_all: boolean = true;

  inDatabase: boolean = false;
  field: string = "desabled";
  icon: string = "fa fa-check";
  label: string = "S'incrire"

  types_inscript: SelectItem[];
  types_connect: SelectItem[];
  selectedType_inscript: string = 'STUDENT';
  selectedType_connect: string = 'STUDENT';
  codeDepartement: string = "";
  classMail: string = "col-lg-12 col-md-12 col-sm-12 col-xs-12";
  valid: boolean = false;
  display: boolean = false;
  message: string = "";
  error: boolean = false;

  constructor(
    private router : Router,
    private route: ActivatedRoute,
    private userService: UserService,
    private utilService: UtilService) {
      this.types_inscript = [
            {label: 'STUDENT', value: 'STUDENT', icon: 'fa fa-fw fa-user'},
            {label: 'TEACHER', value: 'TEACHER', icon: 'fa fa-fw fa-user-md'}
        ];

        this.types_connect = [
              {label: 'STUDENT', value: 'STUDENT', icon: 'fa fa-fw fa-user'},
              {label: 'TEACHER', value: 'TEACHER', icon: 'fa fa-fw fa-user-md'},
              {label: 'ADMIN', value: 'ADMIN', icon: 'fa fa-fw fa-user-secret'}
        ];
    }

  ngOnInit() {
    this.codeDepartement = this.route.snapshot.params['code'];

    this.formreg = new FormGroup({
      matricule: new FormControl('', [Validators.required, Validators.minLength(7), Validators.maxLength(8)]),
      cni: new FormControl('', [Validators.required, Validators.minLength(9), Validators.maxLength(9)]),
      firstname: new FormControl('', []),
      lastname: new FormControl('', []),
      niveau: new FormControl('', []),
      option: new FormControl('', []),
      grade: new FormControl('', []),
      email: new FormControl('', []),
      password: new FormControl('', [Validators.required, Validators.minLength(8)]),
      confirm: new FormControl('', [Validators.required, Validators.minLength(8)])
    }, this.passwordMatchValidator)

    this.formlog = new FormGroup({
      matricule: new FormControl('', [Validators.required, Validators.minLength(7), Validators.maxLength(8)]),
      password: new FormControl('', [Validators.required, Validators.minLength(8)])
    })
  }

  passwordMatchValidator(g: FormGroup) {
   return g.get('password').value === g.get('confirm').value
      ? null : {'validPassword': true};
  }

  showDialog_inscript() {
    this.display_inscrip = true;
    this.display_membre = false;
    this.display_all = false;
  }

  showDialog_membre() {
    this.display_membre = true;
    this.display_inscrip = false;
    this.display_all = false;
    this.display = false;
  }

  closeDialog() {
    this.display_inscrip = false;
    this.display_membre = false;
    this.display_all = true;
    this.router.navigateByUrl('/department/'+this.codeDepartement);
  }

  onConfirm() {
    this.message = "";
    this.error = true;
    this.display = false;
  }

  submit(){
    if(this.selectedType_inscript === 'STUDENT'){
      this.etud.password = this.utilService.crypt(this.formreg.get('password').value);
      this.etud.isRegister = 1;
      this.userService.updateStudent(this.etud)
      .subscribe(data => {this.message = "Succesfully Added account in bank"; this.showState()},
                 error => {this.message = "failed while adding your account"; this.error = true; this.showState()});
    }
    else if(this.selectedType_inscript === 'STUDENT'){
      this.ens.password = this.utilService.crypt(this.formreg.get('password').value);
      this.ens.isRegister = 1;
      this.userService.updateTeacher(this.ens)
      .subscribe(data => {this.message = "Succesfully Added account in bank"; this.showState()},
                 error => {this.message = "failed while adding your account"; this.error = true; this.showState()});
    }
    console.log(this.formreg.value);
  }

  showState() {
    this.display = true;
  }

  connect(){

  }

  async addAccount(){
    this.icon = "fa fa-spinner fa-spin";
    let matricule = this.formreg.get('matricule').value;
    let cni = this.formreg.get('cni').value;

    if(this.selectedType_inscript === 'STUDENT'){
      await this.userService.inscript(this.codeDepartement, 'etudiant', matricule, cni, 0).subscribe(data => {
        this.etud = data.body;
        this.firstname = this.etud.firstName;
        this.lastname = this.etud.lastName;
        this.email = this.etud.email;
        this.inDatabase = (this.etud.idUser != 0)?true:false;
        this.icon = "fa fa-check";
        this.label = "Confirmer";
        this.valid = true;
      });
    }
    else if(this.selectedType_inscript === 'TEACHER'){
      this.classMail = "col-lg-6 col-md-6 col-sm-6 col-xs-12";
      await this.userService.inscript(this.codeDepartement, 'enseignant', matricule, cni, 0).subscribe(data => {
        this.ens = data.body;
        this.firstname = this.ens.firstName;
        this.lastname = this.ens.lastName;
        this.email = this.ens.email;
        this.inDatabase = (this.ens.idUser != 0)?true:false;
        this.icon = "fa fa-check";
        this.label = "Confirmer";
        this.valid = true;
      });
    }
  }

  async mySpace(){
    this.icon = "fa fa-spinner fa-spin";
    let matricule = this.formlog.get('matricule').value;
    let password = this.formlog.get('password').value;

    if(this.selectedType_connect === 'STUDENT'){
      await this.userService.connect(this.codeDepartement, 'etudiant', matricule, password, 1).subscribe(data => {
        this.etud = data.body;
        this.icon = "fa fa-check";
        console.log(this.etud);
      });
    }
    else if(this.selectedType_connect === 'TEACHER'){
      await this.userService.connect(this.codeDepartement, 'enseignant', matricule, password, 1).subscribe(data => {
        this.ens = data.body;
        this.icon = "fa fa-check";
      });
    }
    //this.router.navigateByUrl('/bank');
  }

}



